<template>
    <div class="header">
        <h2>Resource Manager</h2>
    </div>
    <hr>
    <div class="body">
        <div class="actions">
           <div class="row">
            <div class="col-sm-1">
                <label for="filter">Filter by</label>
            </div>
            <div class="col-1">
               <select name="" id="" class="form-select-sm">
                      <option value="">All</option>
                      <option value="">Active</option>
                      <option value="">Inactive</option>
               </select>
            </div>
            <div class="col-4"></div>
            <div class="col-3">
                <input type="text" placeholder="Search ..." class="form-control">
            </div>
            <div class="col">
              <p class="action-button">
                <button class="btn btn-outline-primary " v-bind:title="'Create'" data-bs-toggle="modal" data-bs-target="#staticBackdrop1">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-plus-fill" viewBox="0 0 16 16">
                        <path d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
                        <path fill-rule="evenodd" d="M13.5 5a.5.5 0 0 1 .5.5V7h1.5a.5.5 0 0 1 0 1H14v1.5a.5.5 0 0 1-1 0V8h-1.5a.5.5 0 0 1 0-1H13V5.5a.5.5 0 0 1 .5-.5z"/>
                      </svg>
                      Create
                </button>
                <button class="btn btn-outline-danger " v-bind:title="'Delete'">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-x-fill" viewBox="0 0 16 16">
                        <path fill-rule="evenodd" d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm6.146-2.854a.5.5 0 0 1 .708 0L14 6.293l1.146-1.147a.5.5 0 0 1 .708.708L14.707 7l1.147 1.146a.5.5 0 0 1-.708.708L14 7.707l-1.146 1.147a.5.5 0 0 1-.708-.708L13.293 7l-1.147-1.146a.5.5 0 0 1 0-.708z"/>
                      </svg>
                      Remove
                </button>
              </p>
            </div>
            <hr>
            <div class="table-responsive-sm">
                <table class="table table-striped table-bordered table-hover">
                    <thead>
                        <th><input type="checkbox" id="check"></th>
                        <th>Code</th>
                        <th>Name</th>
                        <th>Description</th>
                        <th>Orders</th>
                        <th>Url</th>
                        <th>Parent name</th>
                        <th>Role</th>
                        <th>Action</th>
                    </thead>
                    <tbody>
                        <tr v-for="item in resources" :key="item">
                            <th class="col">
                                <input type="checkbox" id="check">
                            </th>
                            <td class="col">{{item.code}}</td>
                            <td class="col">{{item.name}}</td>
                            <td class="col">{{item.description}}</td>
                            <td class="col">{{item.order}}</td>
                            <td class="col">{{item.url}}</td>
                            <td class="col">
                                <span  :class="{'badge text-bg-danger':item.resource == null}">
                                    {{item.resource == null ? 'Null parent': item.resource.name}}
                                </span>
                            </td>
                            <td class="col">
                                <span class="badge text-bg-success">detail</span>
                            </td>
                            <td>
                                <span>
                                    <button @click.prevent="updateEntity = item" class="btn btn-outline-primary" v-bind:title="'Edit'" data-bs-toggle="modal" data-bs-target="#staticBackdrop">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pen-fill" viewBox="0 0 16 16">
                                                <path d="m13.498.795.149-.149a1.207 1.207 0 1 1 1.707 1.708l-.149.148a1.5 1.5 0 0 1-.059 2.059L4.854 14.854a.5.5 0 0 1-.233.131l-4 1a.5.5 0 0 1-.606-.606l1-4a.5.5 0 0 1 .131-.232l9.642-9.642a.5.5 0 0 0-.642.056L6.854 4.854a.5.5 0 1 1-.708-.708L9.44.854A1.5 1.5 0 0 1 11.5.796a1.5 1.5 0 0 1 1.998-.001z"/>
                                            </svg>
                                        </button>
                                        <button @click.prevent="deleteById(item.resourceId)" class="btn btn-outline-danger" v-bind:title="'Delete'">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash3" viewBox="0 0 16 16">
                                                <path d="M6.5 1h3a.5.5 0 0 1 .5.5v1H6v-1a.5.5 0 0 1 .5-.5ZM11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3A1.5 1.5 0 0 0 5 1.5v1H2.506a.58.58 0 0 0-.01 0H1.5a.5.5 0 0 0 0 1h.538l.853 10.66A2 2 0 0 0 4.885 16h6.23a2 2 0 0 0 1.994-1.84l.853-10.66h.538a.5.5 0 0 0 0-1h-.995a.59.59 0 0 0-.01 0H11Zm1.958 1-.846 10.58a1 1 0 0 1-.997.92h-6.23a1 1 0 0 1-.997-.92L3.042 3.5h9.916Zm-7.487 1a.5.5 0 0 1 .528.47l.5 8.5a.5.5 0 0 1-.998.06L5 5.03a.5.5 0 0 1 .47-.53Zm5.058 0a.5.5 0 0 1 .47.53l-.5 8.5a.5.5 0 1 1-.998-.06l.5-8.5a.5.5 0 0 1 .528-.47ZM8 4.5a.5.5 0 0 1 .5.5v8.5a.5.5 0 0 1-1 0V5a.5.5 0 0 1 .5-.5Z"/>
                                            </svg>
                                        </button>
                                </span>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
           </div>
        </div>
    </div>
    <!-- Modal create -->
    <div class="modal fade" id="staticBackdrop1" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="staticBackdropLabel">Create</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
      <form @submit.prevent="createResource">
        <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="resource.code">
            <label for="floatingInput">Code</label>
        </div>
        <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="resource.name">
            <label for="floatingInput">Name</label>
        </div>
        <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="resource.description">
            <label for="floatingInput">Description</label>
        </div>
        <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="resource.order">
            <label for="floatingInput">Orders</label>
        </div>
        <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="resource.url">
            <label for="floatingInput">url</label>
        </div>
        <div class="form-floating mb-3">
            <select class="form-select" id="floatingInput" v-model="resource.resource.resourceId">
                <option selected disabled>Choose Parent</option>
                <option v-for="item in resources" :key="item" v-bind:value="item.resourceId">{{item.name}}</option>
            </select>
            <label for="floatingInput">Parent</label>
        </div>
        <div class="row">
            <div class="col-2">
                <button class="btn btn-outline-success">Create</button>
            </div>
        </div>
      </form>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" id="close">Close</button>
      </div>
    </div>
  </div>
</div>
<!-- Update modal -->
<!-- Modal -->
<div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="staticBackdropLabel">Update</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
       <form @submit.prevent="updateResource">
        <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="updateEntity.code" readonly>
            <label for="floatingInput">Code</label>
        </div>
        <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="updateEntity.name">
            <label for="floatingInput">Name</label>
        </div>
        <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="updateEntity.description">
            <label for="floatingInput">Description</label>
        </div>
        <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="updateEntity.order">
            <label for="floatingInput">Orders</label>
        </div>
        <div class="form-floating mb-3">
            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="updateEntity.url">
            <label for="floatingInput">url</label>
        </div>
        <div class="row">
            <div class="col-2">
                <button class="btn btn-outline-success">Update</button>
            </div>
        </div>
       </form>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" id="close1">Close</button>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import axios from 'axios'
export default {
    name: "ResourceManager",
    data() {
        return {
            resources : [],
            resource : {
                code : '',
                name : '',
                description : '',
                order : '',
                url : '',
                resource : {
                    resourceId : '',
                }
            },updateEntity : {
               
            }
        }
    },methods: {
        getResource(){
            axios.defaults.headers.common['Authorization'] = "Bearer "+ localStorage.getItem('token');
            axios.get('/api/resource/get').then(res => {
                this.resources = res.data
            }).catch(e=>{
                e;
                 this.getAccessToken();
            })
        },createResource(){
            axios.post('/api/resource/save',this.resource).then(res => {
                this.getResource();
                document.getElementById('close').click();
                this.$parent.$parent.getDynamicMenu();
                res;
            }).catch(e =>{
                e;
                 this.getAccessToken();
            })
        },deleteById(id){
            axios.delete('/api/resource/delete/'+id).then(res => {
                this.getResource();
                this.$parent.$parent.getDynamicMenu();
                res;
            }).catch(err => {
                console.log(err)
                this.getAccessToken();
            })
        },updateResource(){
            axios.put('/api/resource/update/'+this.updateEntity.resourceId,this.updateEntity).then(res => {
                this.getResource();
                document.getElementById('close1').click();
                this.$parent.$parent.getDynamicMenu();
                res;
                
            })
        },async getAccessToken(){
                axios.post('/api/user/token/refresh')
                    .then( async response => {
                      localStorage.setItem('token',response.data.access_token);
                    })
                    .catch(error => {
                        console.log(error)
                    })
            }
    },created(){
        this.getResource();
    }
}
</script>
<style>
table .col{
    padding-top: 1rem;
}
</style>